import { AID, ERR_OK } from '@/api/config';

import { mapMutations } from 'vuex';

export const commentMixin = {
  data() {
    return {
      // 身份
      isAdmin: true,
      // 详情
      detail: {},
      // 所有评论的回复
      replys: {},
      // 所有的评论
      commentList: [],
      // 标记是否还有更多评论
      hasMoreComment: true,
      // 加载评论的状态
      isLoading: false,
      // 提示
      toast: {
        show: false,
        tip: ''
      },
      // 输入表达占位
      placeholder: '评论'
    }
  },
  computed: {
    // 由于计算scroll的高度
    scrollData() {
      let ret = [];

      for (let key in this.replys) {
        ret = ret.concat(this.replys[key]);
      }
      return this.commentList.concat(ret);
    }
  },
  created() {
    // 获取评论的参数
    this.commentOffset = 0;
    this.commentLimit = 10;

    // 新的评论
    this.newComment = {
      memid: 0, // 获取详情时获得
      name: '', // 获取详情时获得
      avatar: '', // 获取详情时获得
      content: '', // 提交评论时取得
      id: 0,  // 提交评论时返回
      reply_count: 0, // 默认0
      timestamp: '刚刚' // 默认
    };
    // 新的回复
    this.newReply = {
      memid: 0, // 获取详情时获得
      name: '', // 获取详情时获得
      avatar: '', // 获取详情时获得
      admin_id: 0, // 获取详情时获得
      content: '', // 提交回复时取得
      to_name: '', // 提交回复时取得
      timestamp: '刚刚' // 默认
    };
    // 是否是预览
    this.isPreview = Number(this.$route.params.flag) === 0;
    // 获取详情
    this._getDetail();
    // 获取评论
    this._getComment();
  },

  methods: {
    // 获取详情
    _getDetail() {
      const id = this.$route.params.id;

      this.__get_detail_promise({ id }).then((res) => {

        if (res.data.code === ERR_OK) {
          this.detail = res.data.data.detail;

          this.isAdmin = Number(this.detail.isAdmin) === 1;
          this.setUserType({ isAdmin: this.isAdmin });

          this.userId = Number(this.detail.admin_id) ? this.detail.admin_id : this.detail.memid;
          this.setUserId({ id: this.userId });

          // 保存评论需要的信息
          const info = {
            memid: this.detail.memid,
            admin_id: this.detail.admin_id,
            avatar: this.detail.avatar,
            name: this.detail.name
          };

          this.newComment = Object.assign(this.newComment, info);
          this.newReply = Object.assign(this.newReply, info);

        } else {
          const that = this;
          const type = this.$route.params.type;

          this.$dialog.alert({
            mes: res.data.msg,
            callback() {
              if (that.PAGE_TYPE === 'NEWS') {
                that.$router.push(`/news/list/${type}/aid/${AID}`)
              } else if (that.PAGE_TYPE === 'NOTICE') {
                that.$router.push(`/notice/list/aid/${AID}`)
              }
            }
          });
        }

      }).catch((err) => {
        console.error(err);
      });
    },
    // 获取评论
    _getComment() {
      if (this.isPreview) {
        this.hasMoreComment = false;
        return;
      }

      const id = this.$route.params.id;
      const type = this.$route.params.type;
      const limit = this.commentLimit;
      const offset = this.commentOffset;

      this.isLoading = true;

      this.__get_comments_promise({ id, type, limit, offset }).then((res) => {
        if (res.data.code === ERR_OK) {

          this.commentList.push(...res.data.data.list);

          this.isLoading = false;

          if (res.data.data.list.length < limit) {
            this.hasMoreComment = false;
          }

          // 设置对应评论的回复的参数
          if (res.data.data.list.length) {
            this.setReplys(res.data.data.list);
          }
        }

      }).catch((err) => {
        console.error(err);
      });
    },
    // 获取更多评论
    getMoreComment() {

      if (!this.hasMoreComment) {
        return;
      }

      this.commentOffset += 10;

      this._getComment();
    },
    // 设置回复的请求参数
    setReplys(resComments) {
      resComments.forEach(function(item) {

        const id = item.id;

        if (!this.replys[id]) {
          const ret = new Array();

          ret['offset'] = 0;
          ret['limit'] = 5;

          this.$set(this.replys, id, ret);
        }

      }, this);
    },
    // 获取更多回复
    getReply(comment) {
      const id = comment.id;
      const limit = this.replys[id].limit;
      const offset = this.replys[id].offset;
      // const type = this.$route.params.type; // notice 需要type ？？？？
      this.__get_reply_promise({ id, limit, offset }).then((res) => {

        if (res.data.code === ERR_OK) {
          this.replys[id].push(...res.data.data.list);

          this.replys[id].offset += 5;
        }

      }).catch((err) => {
        console.error(err);
      });
    },
    // 点击了评论按钮
    clickCommentBtn() {
      if (this.isPreview) {
        this.$dialog.alert({
          mes: '当前是预览页',
          callback() {}
        });
        return;
      }

      this.placeholder = '评论';

      this.$refs.form.showInput();
      this.SEND_TYPE = 'COMMENT';
    },
    // 点击了评论项
    clickComment(comment, event) {
      if (this.isSelf(comment)) {
        return;
      }

      this.placeholder = `回复 ${comment.name}`

      this.$refs.form.showInput();

      this.SEND_TYPE = 'REPLY_COMMENT';
      // 临时存储
      this.commentData = comment;
    },
    // 点击了回复项
    clickReply(comment, reply) {
      if (this.isSelf(reply)) {
        return;
      }

      this.placeholder = `回复 ${reply.name}`

      this.$refs.form.showInput();

      this.SEND_TYPE = 'REPLY_REPLY';

      // 临时存储
      this.replyData = reply;
      this.commentData = comment;
    },
    // 判断是不是自己
    isSelf(item) {
      const itemUserId = Number(item.admin_id) ? item.admin_id : item.memid;

      return Number(itemUserId) === Number(this.userId);
    },
    // 发送表单
    sendForm(content) {
      // 当前表单是 评论
      if (this.SEND_TYPE === 'COMMENT') {
        this._sendComment(content);
      } else {
        // 当前表单是回复
        this._sendReply(content);
      }
    },
    // 发送评论
    _sendComment(content) {
      const id = this.$route.params.id;
      const type = this.$route.params.type;
      const params = { type, content };
      // 判断是新闻还是通知 来确定字段
      if (this.PAGE_TYPE === 'NEWS') {
        params.news_id = id;
      }
      if (this.PAGE_TYPE === 'NOTICE') {
        params.notice_id = id;
      }


      this.__send_comment_promise(params).then((res) => {
        if (res.data.code === ERR_OK) {
          this.showToast({ tip: '评论已发送' });

          // 更新评论列表
          const id = res.data.data.id;
          const item = Object.assign({}, this.newComment, { id, content })

          this.commentList.unshift(item);
          this.commentOffset++;

        } else {
          this.$dialog.alert({
            mes: res.data.msg,
            callback() {}
          });
        }

      }).catch((err) => {
        console.error(err);
      });
    },
    // 发送回复
    _sendReply(content) {
      const id = this.$route.params.id;
      const type = this.$route.params.type;
      // 从临时存储中取出被点击的评论或回复
      const comment_id = this.commentData.id;
      let TO_KEY = '';
      let TO_ID = 0;
      let TO_NAME = '';
      // 判断是被回复的 是管理员 还是 非管理员
      if (this.SEND_TYPE === 'REPLY_REPLY') {
        const adminId = Number(this.replyData['admin_id']);
        TO_KEY = adminId ? 'to_adminid' : 'to_memid';
        TO_ID = adminId ? this.replyData['admin_id'] : this.replyData['memid']
        TO_NAME = this.replyData.name;
      }
      if (this.SEND_TYPE === 'REPLY_COMMENT') {
        const adminId = Number(this.commentData['admin_id']);
        TO_KEY = adminId ? 'to_adminid' : 'to_memid';
        TO_ID = adminId ? this.commentData['admin_id'] : this.commentData['memid']
        TO_NAME = this.commentData.name;
      }

      const params = { comment_id, content };
      // 判断是新闻或是通知确定字段
      if (this.PAGE_TYPE === 'NOTICE') {
        params.notice_id = id;
        params.type = type;
      }
      if (this.PAGE_TYPE === 'NEWS') {
        params.news_id = id;
      }
      params[TO_KEY] = TO_ID;

      this.__send_reply_promise(params).then((res) => {
        if (res.data.code === ERR_OK) {
          this.showToast({ tip: '回复已发送' });

          const id = res.data.data.id;

          const item = Object.assign({}, this.newReply, { id, content, to_name: TO_NAME });

          const index = this.commentList.findIndex((item) => {
            return Number(item.id) === Number(comment_id);
          });

          this.commentList[index].reply_count++;

          this.replys[comment_id].unshift(item);

          this.replys[comment_id].offset++;

        } else {
          this.$dialog.alert({
            mes: res.data.msg,
            callback() {}
          });
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    // 显示提示
    showToast({ tip }) {
      this.toast.tip = tip;
      this.toast.show = true;

      setTimeout(function() {
        this.toast.show = false;
      }.bind(this), 1500)
    },
    refreshScroll() {
      this.$refs.scroll.refresh();
    },
    ...mapMutations({
      setUserType: 'SET_USER_TYPE',
      setUserId: 'SET_USER_ID'
    })
  }
}



