<template>
  <div class="form-wrapper">
    <transition name="fade">
      <div class="mask" @click.stop="hideInput" v-show="showForm" @touchstart.stop></div>
    </transition>
    <div class="form" v-show="showForm">
      <textarea class="input" autofocus="autofocus" ref="input"
            :placeholder="placeholder"
            @blur="hideInput"
            @touchstart.stop
      >
      </textarea>
      <button @click.stop="send">确定</button>
    </div>
  </div>
</template>

<script type="ecmascript-6">
export default {
  props: {
    placeholder: {
      type: String,
      default: '评论'
    }
  },
  data(){
    return {
      showForm: false
    }
  },
  methods: {
    hideInput() {
      const content = this.$refs.input.value.trim();
      if (!content) {
        this.showForm = false;
        this.$refs.input.value = '';
      }
    },
    showInput() {
      this.showForm = true;

      setTimeout(function() {
        this.$refs.input.focus();
      }.bind(this), 50);
    },
    send() {
      const content = this.$refs.input.value.trim();

      if (!content) {
        return;
      }

      this.showForm = false;

      this.$refs.input.value = '';

      this.$emit('send', content)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";
@import "~@/common/stylus/mixin"

animation-fade(0.3s)

.form-wrapper
  .mask
    position fixed
    top 0
    left 0
    bottom 0
    right 0
    background-color rgba(0, 0, 0, .3)
  .form
    display flex
    position absolute
    left 0
    bottom 0
    width 100%
    padding-bottom 0.1rem
    padding-top 0.1rem
    padding-right 0.12rem
    background-color #fff
    z-index 10
    .input
      flex-grow 1
      margin 0 0.15rem
      padding 0.03rem 0.08rem
      border 1px solid $color-main
      text-align justify
      font-size 0.36rem
      outline none
    button
      align-self flex-end
      flex-shrink 0
      align-self flex-end
      width 1rem
      height 0.55rem
      border none
      border-radius 0.05rem
      background-color $color-main
      color #fff
      font-size 0.3rem
</style>



