<template>
    <div class="comment" @click="clickComment(comment, $event)">
      <div class="avatar">
        <img :src="comment.avatar || defaultAvatar" alt="头像">
      </div>
      <div class="text">
        <span class="nickname">{{comment.name}}</span>
        <p class="message">{{comment.content}}</p>
        <span class="time">{{comment.timestamp}}</span>
        <!-- 回复列表 -->
        <div class="reply-list" v-show="comment.reply_count || replyList.length">
          <transition-group tag="ul" name="fade">
            <li v-for="(reply, index) in replyList" :key="index" @click.stop="clickReply(reply, comment, $event)">
              <div class="avatar">
                <img :src="reply.avatar || defaultAvatar" alt="头像">
              </div>
              <div class="text">
                <span class="nickname">{{reply.name}}</span>
                <span class="txt-grey" v-show="reply.to_name">回复</span>
                <span class="nickname">{{reply.to_name}}</span>
                <p class="message">{{reply.content}}</p>
                <span class="time">{{reply.timestamp}}</span>
              </div>
            </li>
          </transition-group>
          <span class="get-reply" v-show="replyLeft(comment) > 0" @click.stop="getReply(comment)">{{replyLeft(comment)}}条回复</span>
        </div>
      </div>
    </div>
</template>

<script type="ecmascript-6">
import defaultAvatar from '@/common/images/default-avatar.png';

export default {
  props: {
    // 评论的回复
    replyList: {
      type: Array,
      required: true,
      default() {
        return []
      }
    },
    comment: {
      type: Object,
      required: true,
      default() {
        return {}
      }
    }
  },
  created() {
    this.defaultAvatar = defaultAvatar;
  },
  methods: {

    // 计算剩余的回复条数
    replyLeft(comment) {
      return comment.reply_count - this.replyList.length;
    },

    // 点击了回复项
    clickReply(comment, reply, event) {
      this.$emit('clickReply', reply, comment, event);
    },
    // 点击了评论项
    clickComment(comment, event) {
      this.$emit('clickComment', comment, event);
    },
    // 点击了获取按钮
    getReply(comment) {
      this.$emit('getReply', comment);
    }
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable"
@import "~@/common/stylus/mixin"

animation-fade(0.4s)
.comment
  display flex
  margin-bottom 0.3rem
  .avatar
    flex-shrink 0
    img
      width 0.88rem
      height 0.88rem
      background-color #ccc
  .text
    flex-grow 1
    margin-left 0.33rem
    .nickname
      margin-bottom 0.1rem
      font-size 0.36rem
    .txt-grey
      padding 0 0.05rem
      font-size 0.34rem
      color $color-txt-grey
    .message
      margin-top 0.1rem
      margin-bottom 0.1rem
      text-align justify
      font-size 0.32rem
      color #444
    .time
      font-size 0.28rem
      color #999
    .reply-btn
      margin-left 0.3rem
      font-size 0.34rem
      color lighten($color-link, 30%)
    .reply-list
      margin-top 0.1rem
      &>ul>li
        display flex
        margin-bottom 0.12rem
        .avatar
          flex-shrink 0
        .text
          flex-grow 1
          margin-bottom 0.2rem
      .get-reply
        font-size 0.34rem
        color lighten($color-link, 30%)
</style>





