<template>
  <scroll class="news-detail" :pullup="true" :data="scrollData" @scrollToEnd="getMoreComment" ref="scroll">
    <div>
      <!-- 详情 -->
      <detail-panel :detail="detail" @refreshScroll="refreshScroll"/>
      <!-- 评论 -->
      <div class="detail-comment">
        <!-- 分割线 -->
        <divide-line text="评论" />
        <!-- 评论按钮 -->
        <div class="btn-comment" :class="{'show': !isAdmin}">
          <button class="edit" @click="clickCommentBtn">
            <img src="../../assets/icon-edit.png">
            <span>评论</span>
          </button>
        </div>
        <!-- 评论和回复列表 -->
        <transition-group name="fade-slide-up" tag="div" class="comments">
          <comment-panel v-for="comment in commentList"
                          :key="comment.id"
                          :comment="comment"
                          :reply-list="replys[comment.id]"
                          @clickComment="clickComment"
                          @clickReply="clickReply"
                          @getReply="getReply"
          />
        </transition-group>
        <!-- 加载状态 -->
        <load-more :over="!hasMoreComment" :is-loading="isLoading"/>

      </div>
    </div>
    <!-- 输入表单 -->
    <reply-form @send="sendForm" :placeholder="placeholder" ref="form"/>
    <!-- toast -->
    <success :show="toast.show" :tips="toast.tip"/>
    <!-- /scrollend -->
  </scroll>
</template>

<script type="ecmascript-6">
import { getNewsDetail, getNewsPreview, getNewsComment, sendNewsComment, getNewsReply, sendNewsReply } from '@/api/news';

import { commentMixin } from '@/common/js/mixin';

import Scroll from '@/base/scroll/scroll';
import LoadMore from '@/base/load-more/load-more';
import DetailPanel from '@/base/panel/news-notice-detail';
import DivideLine from '@/base/line/divide-line';
import Success from '@/base/toast/success';
import CommentPanel from '@/base/panel/comment';
import ReplyForm from '@/base/form/reply';

export default {
  mixins: [commentMixin],
  created() {
    this.PAGE_TYPE = 'NEWS';
  },
  methods: {
    __get_detail_promise(params) {
      return this.isPreview ? getNewsPreview(params) : getNewsDetail(params);
    },
    __get_comments_promise(params) {
      return getNewsComment(params);
    },
    __get_reply_promise(params) {
      return getNewsReply(params);
    },
    __send_comment_promise(params) {
      return sendNewsComment(params);
    },
    __send_reply_promise(params) {
      return sendNewsReply(params);
    }
  },
  components: {
    Scroll,
    DetailPanel,
    DivideLine,
    CommentPanel,
    LoadMore,
    Success,
    ReplyForm
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";

.news-detail
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  overflow hidden
  .detail-comment
    padding 0 0.4rem
    .btn-comment
      display none
      text-align right
      &.show
        display block
      .edit
        display inline-block
        border none
        img
          width 0.36rem
          height 0.4rem
          margin-right 0.08rem
        span
          font-size 0.38rem
</style>



