<template>
  <transition name="fade">
    <div class="success-toast" v-show="show">
      <i class="icon-success-no-circle"></i>
      <p class="tip">{{tips}}</p>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    tips: {
      type: String,
      default: '成功了'
    }
  },
  watch: {
    show(newVal) {
      this.show = newVal
    }
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";
@import "~@/common/stylus/mixin";
@import "~@/common/stylus/icon";

animation-fade(0.5s)

.success-toast
  position absolute
  top 35%
  left 50%
  height 2.4rem
  width 2.4rem
  background-color $toast-bg
  border-radius 5px
  transform translate3d(-50%, -50%, 0)
  text-align center
  color #fff
  .icon-success-no-circle
    margin 0.36rem 0 0.16rem
    font-size 1.1rem
  .tip
    overflow hidden
    font-size 0.36rem


</style>




