<template>
  <div class="panel" @touchstart="refreshScroll">
    <p class="title">{{detail.title}}</p>
    <div class="time-author">
      <span>{{detail.timestamp}}</span>
      <span>{{detail.author}}</span>
    </div>
    <div class="abstract">{{detail.abstract}}</div>
    <div class="detail-content">
      <pre v-if="detail.type === '2'" v-html="detail.content"></pre>
      <div v-else v-html="detail.content"></div>
      <ul class="attachment" v-if="detail.type === '3'">
        <li>
          <a class="down" :href="detail.filepath" @click="download(detail)">
            <span>{{detail.filename}}</span>
            <button>下载</button>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="ecmascript-6">
export default {
  props: {
    detail: {
      type: Object,
      required: true
    }
  },
  methods: {
    refreshScroll() {
      this.$emit('refreshScroll')
    },
    download(item) {
      window.open(item.filepath, item.filename)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/variable";

.panel
  padding 0.2rem
  margin-bottom 0.3rem
  background-color #fff
  box-shadow 0 2px 8px 1px #ddd
  .title
    margin-bottom 0.2rem
    font-size 0.48rem
  .time-author
    margin-bottom 0.18rem
    font-size 0.34rem
    color #777
    span
      margin-right 0.5rem
  .detail-content
    padding 0.3rem
    font-size 0.38rem
    word-break break-all
    pre
      white-space pre-wrap
    .attachment>li>a
      display flex
      justify-content space-between
      span
        font-size 0.32rem
        text-decoration underline
        line-height 1.3
      button
        flex-shrink 0
        align-self center
        border 0.01rem solid $color-main
        border-radius 0.08rem
        padding 0.08rem 0.15rem
        background-color #fff
        color $color-main
        font-size 0.3rem
  .abstract
    font-size 0.36rem
    color #666
</style>





