<template>
  <ul class="divide-line">
    <li></li>
    <li v-show="text">{{text}}</li>
    <li></li>
  </ul>
</template>

<script type="ecmascript-6">
export default {
  props: {
    text: {
      type: String,
    }
  }
}
</script>

<style lang="stylus" scoped>
.divide-line
  display flex
  margin-bottom 0.2rem
  li
    flex-shrink 1
    align-self center
    font-size 0.36rem
    &:nth-child(2)
      margin 0 0.2rem
    &:first-child, &:last-child
      flex-grow 1
      border-bottom 0.01rem solid #ccc
</style>





